@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
.text {
    mix-blend-mode: exclusion;
    font-family: 'Inconsolata', monospace;
    color: #fff;
}
:root {
    --shadow-1: #1e222530;
    --shadow-2: #1e222560;
    --light-1: #ffffff60;
}

:root .dark * {
    --bg-1: #52585a;
    --bg-2: #1e2327;
    --bg-btn: #3A3D40;
    --off: #ffffff60;
}

:root .light * {
    --bg-1: #dff7ff;
    --bg-2: #8badc9;
    --bg-btn: #a0b1b9;
    --off: #00000060;
}
.button {
    background-color: var(--bg-btn);
    box-shadow: -1px 1px 0px 1px var(--shadow-2), 1px -1px 2px 1px var(--light-1), inset -1px 1px 1px 0px var(--shadow-2), inset -2px 2px 1px 0px var(--light-1);
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 1.5rem;
    height: 100%;
}

.button.equal {
    grid-area: e;
}
.display {
    grid-area: d;
    justify-self: end;
    align-self: end;
    height: 100%;
    width: calc(100% - 1rem);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column-reverse nowrap;
            flex-flow: column-reverse nowrap;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-justify-content: end;
            justify-content: end;
    outline: 1px solid var(--off);
    outline-offset: 7px;
    box-shadow: inset 4px 6px 25px -5px var(--shadow-1);
    transition: .6s;
    overflow: hidden;
    padding-bottom: 5px;
    padding-inline: .5rem;
}

.display :first-child {
    font-size: 4rem;
}

.display .text {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    text-align: end;
}
.calculator {
    background-image: radial-gradient(circle at top left, var(--bg-1), var(--bg-2));
    border-radius: 0.8rem;
    padding: 1.5rem 1rem;
    font-size: 1.5em;
    box-shadow: -.7rem .4rem 1rem var(--shadow-2);
    display: grid;
    grid-template-columns: 3.5rem 3.5rem 3.5rem 3.5rem;
    grid-template-rows: 6rem 2.5rem 2.5rem 2.5rem 2.5rem 2.5rem 2.5rem 2.5rem;
    grid-template-areas: "d d d d" "s s s ." ". . . ." ". . . ." ". . . ." ". . . ." ". . . e" ". . . e";
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 15px 10px;
    gap: 15px 10px;
}

/*max-height: 32rem;
height: 32rem;
min-height: 31rem;*/
div.switch {
  grid-area: s;
  font-size: .8rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: safe;
          justify-content: safe;
  width: 100%;
}

.switch .text {
  margin-inline: 1em;
  -webkit-flex-basis: auto;
          flex-basis: auto;
  display: inline-block;
}

.switcher {
  position: relative;
  width: 2.6rem;
  height: 1.5rem;
}

.switcher input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient( 160deg, var(--bg-2), var(--bg-1));
  -webkit-transition: .4s;
  box-shadow: inset 2px -1px 3px -1px #00000060;
  transition: .2s;
  border-radius: 200px;
}

.slider:before {
  position: absolute;
  content: "";
  box-shadow: inset 2px -2px 3px 10px var(--shadow-1), inset 2px -2px 3px 1px var(--shadow-2);
  ;
  height: 1rem;
  width: 1rem;
  left: 4px;
  bottom: 4px;
  background-color: var(--off);
  transition: .4s;
  border-radius: 50%;
}

input:focus+.slider {
  box-shadow: inset 6px -4px 5px -3px var(--shadow-2);
}

input:checked+.slider:before {
  -webkit-transform: translateX(1rem);
  transform: translateX(1rem);
  box-shadow: inset 6px -8px 4px 8px var(--shadow-1), inset 4px -6px 4px var(--shadow-2);
}
.screen {}
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background-image: radial-gradient(circle 120vw at center right, #ab76ffb9, #3a6ebd93);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

body {
    margin: auto;
}

#root {
    margin: auto;
}
